* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

h2,
p {
  margin: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.cat-info {
  display: flex;
  gap: 20px;

  margin-top: 10px;
}

.text-header {
  margin-bottom: 10px;
}
.text-temperament {
  margin-top: 10px;
}

.text-temperament span {
  font-weight: 700;
}

.loader,
.error {
  font-weight: 500;
}

.error {
  color: #ff0000;
}

.hidden {
  display: none;
}
